<template>
  <div id="main">
    <header>
      <!---->
      <TopMenu />
    </header>

    <!---->
    <div class="menu-card">
      <router-view></router-view>
    </div>

    <!---->
    <div class="login" v-if="hassignin" v-on:click="cancelsignin">
      <form v-on:submit.prevent="signIn" class="loginform" v-on:click="loginform">
        <h2></h2>
        <div class="inputItem">
          <label for="username">username</label>
          <input type="text" v-model="username" id="username">
        </div>
        <div class="inputItem">
          <label for="password">password</label>
          <input type="text" v-model="password" id="password">
        </div>
        <button>signin</button>
        <p>usernamerootpasswordpassword</p>
        <p>usernameonepasswordone</p>
        <p>usernametwopasswordtwo</p>
      </form>
    </div>
    <!---->
    <div class="login" v-if="hassignup" v-on:click="cancelsignup">
      <form v-on:submit.prevent="signUp" class="loginform" v-on:click="loginform">
        <h2>(</h2>
        <div class="inputItem">
          <label for="username">username</label>
          <input type="text" v-model="username" id="username">
        </div>
        <div class="inputItem">
          <label for="password">password</label>
          <input type="text" v-model="password" id="password">
        </div>
        <button>signup</button>
      </form>
    </div>

    <!--{{this.$store.state}}-->

    <Footer></Footer>

  </div>
</template>

<script>
import axios from 'axios'
import TopMenu from '@/components/main/TopMenu.vue'
import Footer from '@/components/main/Footer.vue'

export default {
  name: 'Main',
  components: {
    TopMenu,
    Footer,
  },
  data:function(){
    return {
      username:'',
      password:'',
    }
  },
  created:function(){
    var that = this
    //axiosArticle
    //Store
    axios.get('/article/articles')
    .then(function(response){
      //console.log(response.data)
      that.$store.commit('initialArticles',response.data)
    }).catch(function(error){
      console.log(error)
    })

    axios.get('/question/questions')
    .then(function(response){
      console.log(response.data)
      //question module
      that.$store.commit('initialQuestions',response.data)
    }).catch(function(error){
      console.log(error)
    })
  },
  computed:{
    hassignin:function(){ //
      return this.$store.state.user.hassignin
    },
    hassignup:function(){ //
      return this.$store.state.user.hassignup
    }
  },
  methods:{
    //
    loginform:function(event){
      event.stopPropagation();
    },
    cancelsignin:function(){ //
      this.$store.commit('editsignin')
      this.username=''
      this.password=''
    },
    cancelsignup:function(){ //
      this.$store.commit('editsignup')
      this.username=''
      this.password=''
    },
    //
    signIn:function(){
      const that = this;
      axios.post('/user/signin',{
        username:that.username,
        password:that.password
      }).then(function(response){
        console.log(response.data)
        if(response.data){
          //
          var user={
            userid:response.data.userid,
            username:response.data.username,
            password:response.data.password
          }
          that.$store.commit('editlogin',user);
          //isliked
        }else{
          alert("")
        }
        that.cancelsignin();
      }).catch(function(error){
        console.log(error)
        alert("");
        that.cancelsignin();
      })
    },
  }
}
</script>

