const user = {
	state:{
		haslogin: false,  //
		hassignin: false, //
        hassignup: false, //
		avatar:"", //URL
		userid:0,
		username: "", //
		password: "", //
	},
	mutations:{
		editlogin(state,user){
			state.haslogin=true
			state.userid=user.userid
			state.username=user.username
			state.password=user.password
		},
		editsignin(state){
			state.hassignin=!state.hassignin
		},
		editsignup(state){
			state.hassignup=!state.hassignup
		},
	}
}

export default user;